.topbar {
  width: 100%;
  background-color: rgb(255, 255, 255);
  position: sticky;
  top: 0;
  z-index: 999;
  padding: 20px 0;
}

.topbarWrapper {
  
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
}
.topLeft{
  max-width: 300px;
}
.topRight{
    display: flex;
    align-items: center;
}
.topLeft img{
  width: 100%;
}
.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}

.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}